.Anno1800Helper {
  background-color: #282c34;
  color: white;
  min-height: 100vh;
}

.Anno1800Helper-Header {
  background-color: #070A15;
}

.Anno1800Helper-PageHeader {
  background-color: transparent;
}

.Anno1800Helper-PageHeader-title {
  color: white;
  font-size: large;
}

.Anno1800Helper-LanguageSelector{
}

.Anno1800Helper-content {
  padding-top: 10px;
  max-width: 800px;
  align-self: center;
  justify-content: center;
}

.Anno1800Helper-footer {
  text-align: center;
  background-color: #070A15;
  color: white;
}

.Anno1800Helper-CalculateButton {
  padding-top: 30px;
}

.Anno1800Helper-PopulationLevelInput {
  padding-top: 10px;
  text-align: center;
}
.Anno1800Helper-ProductionOverview {
  padding-top: 30px;
}

.Anno1800Helper-FactoryContainer {
  background-color: #424f51;
  border: 2px solid black;
  border-top: none;
  border-left: none;
  border-radius: 5px;
}

.Anno1800Helper-link {
  color: #61dafb;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

